import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)


/* page */
const open = () => import('@/views/open')
const tousuLayout = () => import('@/views/tousu/layout')
const tousuList = () => import('@/views/tousu/list')
const tousuSubmit = () => import('@/views/tousu/submit')

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      name: 'open',
      component: open,
      meta:{
        title: '　　　'
      }
    },  
    {
      path: '/tousu',
      redirect:'/tousu/tousuList',
      component: tousuLayout,
      meta:{
        title: '　　　'
      },      
      children: [
        {
          path: 'tousuList',
          name: 'tousuList',
          component: tousuList
        },
        {
          path: 'tousuSubmit',
          name: 'tousuSubmit',
          component: tousuSubmit,
          beforeEnter: (to, from, next) => {
            if( to.params.m  == "sB*BNOkvaWJ?Th5tE4W90I*?55umoP$<" ){
            //if( to.params.m  == "a" ){ 
              next()
            }else{
              next('/')
            }
          }
        }           
      ]
    },
    { path: '*', redirect: '/' }
  ]
})



